import colorPicker from "@/components/colorPicker.vue";
import uniPopup from "@/components/uni-popup/uni-popup.vue";
import topbar from "./header.vue";
export default {
    components: {
        colorPicker,
        uniPopup,
        topbar
    },
    props: {
        html: {
            type: String
        },
        imageUploader: {
            type: Function
        },
        muiltImage: {
            type: Boolean,
            default: false
        },
        compressImage: {
            type: Boolean,
            default: false
        },
        previewMode: {
            type: Boolean,
            default: false
        },
        tools: {
            type: Array,
            default: function() {
                return [
                    "bold",
                    "italic",
                    "underline",
                    "strike",
                    "align-left",
                    "align-center",
                    "align-right",
                    "list-bullet",
                    "list-ordered",
                    "color",
                    "backgroundColor",
                    "clear",
                    "preview",
                    "image",
                    "h1",
                    "h2",
                    "h3"
                ];
            }
        }
    },
    data() {
        return {
            show: true,
            readOnly: false,
            formats: {},
            fontColor: "#000000",
            bgColor: "",
            color: "",
            colorPickerName: "",
            showColor: false,
            fontSizeRange: [10, 12, 14, 16, 18, 24, 32],
            showPreview: false,
            htmlData: "",
            title: ""
        };
    },
    mounted: function() {
        if (this.previewMode) {
            this.previewData(this.html);
        }
    },
    computed: {
        labelConfirm: function() {
            return this.showPreview ? "关闭" : "保存";
        },
        labelCancel: function() {
            return this.showPreview ? "" : "取消";
        }
    },
    methods: {
        setTitle(value) {
            this.title = value === "" ? this.$filter(Date.now()) : value;
        },
        openColor(e) {
            var name = e.currentTarget.dataset.name;
            var color = this.formats[name];
            this.colorPickerName = name;
            if (name == "backgroundColor" && !color) {
                color = "#FFFFFF";
            }
            if (name == "color" && !color) {
                color = "#000000";
            }
            this.color = color;
            this.$refs.popup.open({
                type: "bottom"
            });
        },
        colorPop(e) {
            this.showColor = e.show;
        },
        colorPopClose() {
            this.$refs.popup.close();
        },
        colorChanged(e) {
            let label = "";
            switch (this.colorPickerName) {
                case "backgroundColor":
                    if (e.color == "#FFFFFF") {
                        e.color = "";
                    }
                    this.bgColor = e.color;
                    label = "背景色";
                    break;
                case "color":
                    this.fontColor = e.color;
                    label = "颜色";
                    break;
            }
            this.colorPopClose();
            this._format(this.colorPickerName, e.color, label + e.color);
        },
        readOnlyChange() {
            this.readOnly = !this.readOnly;
        },
        onEditorReady() {
            uni.createSelectorQuery()
                .in(this)
                .select("#editor")
                .context(res => {
                    this.editorCtx = res.context;
                    if (this.html) {
                        this.editorCtx.setContents({
                            html: this.html
                        });
                    }
                })
                .exec();
        },
        undo() {
            this.editorCtx.undo();
            this.toast("撤销");
        },
        redo() {
            this.editorCtx.redo();
            this.toast("重做");
        },
        format(e) {
            let { name, value, label } = e.target.dataset;
            if (!name) return;
            this._format(name, value, label);
        },
        _format(name, value, label) {
            this.editorCtx.format(name, value);
            this.toast(label);
        },
        toast(label) {
            uni.showToast({
                duration: 600,
                icon: "none",
                title: label
            });
        },
        onStatusChange(e) {
            const formats = e.detail;
            this.formats = formats;
        },
        insertDivider() {
            this.editorCtx.insertDivider({
                success: function() {
                    this.toast("插入分割线");
                }
            });
        },
        clear() {
            this.editorCtx.clear({
                success: res => {
                    this.toast("清空");
                }
            });
        },
        removeFormat() {
            this.editorCtx.removeFormat();
            this.toast("清除格式");
        },
        insertDate() {
            const date = new Date();
            const formatDate = `${date.getFullYear()}/${date.getMonth() +
                1}/${date.getDate()}`;
            this.editorCtx.insertText({
                text: formatDate
            });
            this.toast("插入日期");
        },
        insertImage() {
            let params = {};
            params.count = this.muiltImage ? 9 : 1;
            params.sizeType = this.compressImage
                ? ["compressed"]
                : ["original"];
            uni.chooseImage({
                ...params,
                success: res => {
                    res.tempFilePaths.map(path => {
                        this.imageUploader(path, url => {
                            this.editorCtx.insertImage({
                                src: url,
                                alt: "图片",
                                fail() {
                                    console.error("图片插入失败:" + url);
                                }
                            });
                        });
                    });
                }
            });
        },
        fontSize(e) {
            const index = e.detail.value;
            const fz = this.fontSizeRange[index] + "px";
            this._format("fontSize", fz, "字体大小:" + fz);
        },
        cancel() {
            this.$emit("cancel");
        },
        save() {
            if (this.showPreview) {
                if (this.previewMode) {
                    this.cancel();
                } else {
                    this.showPreview = false;
                }
            } else {
                this.editorCtx.getContents({
                    success: res => {
                        this.$emit("save", { ...res, title: this.title });
                    }
                });
            }
        },
        previewData: function(html) {
            this.htmlData = html.replace(
                /\<img/gi,
                '<img style="max-width:50%;height:auto"'
            );
            this.showPreview = true;
        },
        preview: function() {
            this.editorCtx.getContents({
                success: res => {
                    this.previewData(res.html);
                }
            });
        }
    }
};
